<template>
  
<div class="content-overflow"
  
  >
  <!-- style="max-height: calc(100vh - 350px);" -->
  <div class="icon-title-wrapper"
  :class="{ 'icon-title-resume': actualView.InternalID === 1 || actualView.isResume,
            'icon-title-border': isWrapperBorder,
            }">
    <InfoImage
          v-if="actualView.bodyContent.find(item => item.contentImage && item.contentImage.some(img => img.isIcon))" 
          :contentImage="actualView.bodyContent.find(item => item.contentImage && item.contentImage.some(img => img.isIcon)).contentImage"
          :onGeneralBorder="true"
        />
    <InfoTitle
        v-if="actualView.bodyContent.find(item => item.contentTitle)"
        :contentTitle="actualView.bodyContent.find(item => item.contentTitle).contentTitle"
        class="upper-title"
      />   
  </div>
  <div class="content-wrapper-border" v-if="isWrapperBorder
                                          "></div>
  <div class="content-wrapper">
  
    <div class="content-body"
    :class="{'content-no-border': isWrapperBorder,
            'content-body-border': !item.contentTitle && !item.contentImage && actualView.InternalID != 1 && !actualView.isResume && !actualView.GeneralBorder, 
            'content-first-border': !item.contentTitle && !item.contentImage && actualView.InternalID === 1,
            'content-resume-border': !item.contentTitle && !item.contentImage && actualView.isResume,
            'content-body-image': item.contentImage && !item.contentImage.some(img => img.isIcon) && actualView.InternalID != 1 && !actualView.isResume,
            
            'content-body-qr': item.contentQR,
            
            'content-title': item.contentTitle}"
        v-for="(item,index) in actualView.bodyContent"
        :key="index"
    >
        <div>

        </div>

        <InfoText
          v-if="item.contentText"
          :contentText="item.contentText"
          :InternalID="actualView.InternalID"
          :centerText="actualView.InternalID === 1 || actualView.isResume"
          :isWrapperBorder="isWrapperBorder"
          :isUnderImage="isUnderImage(index)"
          @update-lines="updateTotalLines"
        >            
        </InfoText>
        
        <InfoImageText
          v-if="item.contentImageText"
          :image="item.contentImageText.image"
          :text="item.contentImageText.text"
        >
        </InfoImageText>


        <InfoQR
          v-if="item.contentQR"
          :contentQR="item.contentQR"
        >            
        </InfoQR>
        
        <ChartQR
          v-if="item.contentChart"
        >            
        </ChartQR>        

        <InfoImage
          v-if="item.contentImage &&  !item.contentImage.some(img => img.isIcon)"
          :contentImage="item.contentImage"
          :onGeneralBorder="false"
          :centerImage="actualView.InternalID === 1 || actualView.isResume"
          :totalLines="totalLines"
        >
        </InfoImage>

        <InfoPercent
          v-if="item.contentPercent"
        >
        </InfoPercent>
        
    </div>

    <!-- <AcceptCheckbox
      v-if="actualView.acceptCheckbox"  
      :isAccept=isAccept
      @changeAccept = changeAccept
    /> -->

  <!-- </div> -->

  </div>
  
    <UserFeedback
        v-if="actualView.feedback"
      />

  </div>
  </template>
  
 <script>
import { mapActions } from 'vuex'
import { mapGetters } from 'vuex'
import InfoTitle from '@/components/InfoTitle.vue'
import InfoText from '@/components/InfoText.vue'
import InfoQR from '@/components/InfoQR.vue'
import ChartQR from '@/components/ChartQR.vue'
import InfoImage from '@/components/InfoImage.vue'
import UserFeedback from '@/components/UserFeedback.vue'
import InfoPercent from "@/components/InfoPercent.vue"
import InfoImageText from '@/components/InfoImageText.vue'

export default {
    name: "InfoContent",
    data() {
      return {
        isAccept: false,
        questionsObject: {
        question: "Как дела?",
        answers: [
          {
            id: 1,
            name: "Норм",
          },
          {
            id: 2,
            name: "Не норм",
          },
        ],
        type: 1,
      },
      totalLines: 0,
      linesPerComponent: {}

      }
    },
    components: {
        InfoTitle,
        InfoText,
        InfoImage,
        UserFeedback,
        InfoPercent,
        InfoQR,
        ChartQR,
        InfoImageText
    },
    computed: {
      ...mapGetters([
          "gameData",
          "stepNumber",
          "actualView",
          "actualProperty"
      ]),
      isWrapperBorder() {
        return !this.actualView.isResume &&
              this.actualView.bodyContent &&
              (this.actualView.bodyContent.some(item => item.contentImageText) ||
              (this.actualView.bodyContent.some(item => item.contentImage && item.contentImage.some(img => !img.isIcon)) &&
              this.actualView.bodyContent.some(item => item.contentImage && item.contentImage.some(img => img.isIcon)) &&
              this.actualView.bodyContent.some(item => item.contentText)));
      },
      // classBodyContent(){
      //   if (this.actualView.acceptCheckbox) {
      //     return "px-28 py-6 border-b border-slate-700 flex flex-col"
      //   }
      //   else {
      //     return "px-12 py-6 flex-col flex items-center overflow-y-auto"
      //   }
      // }        
    },
    methods: {
      ...mapActions([
          "setAcceptCheckbox"
      ]),

      updateTotalLines(internalID, lines) {
        this.linesPerComponent = {
          ...this.linesPerComponent,
          [internalID]: lines
        };
        this.totalLines = Object.values(this.linesPerComponent).reduce((sum, lines) => sum + lines, 0);
        console.log("totalLines: " + this.totalLines)
      },

      changeAccept() {
        this.isAccept = !this.isAccept
        this.setAcceptCheckbox(this.isAccept)
      },

      isUnderImage(index) {
        // Ищем индекс предыдущего элемента с contentImage
        for (let i = index - 1; i >= 0; i--) {
          if (this.actualView.bodyContent[i].contentImage && !this.actualView.bodyContent[i].contentImage.some(img => img.isIcon)) {
            return true;
          }
        }
        return false;
      },

    }

}
</script>

