<template>
    <div class="progress-wrapper">

        <div class="progress-image-column">
            <div class="progress-image">
                <img
                    v-if="rightAnswer && showRightAnswers"
                    :src='getImage()' alt="image">
            </div>
        </div>

        <div class="progress-text-bar">
            <div class="progress-text" v-html="formattedProgressText"></div>

            <div class="progress" :class="{'progress-wrong': showRightAnswers != undefined && showRightAnswers == true && rightAnswer == false}">
                <div class="progress-bar" :class="{'progress-bar-wrong': showRightAnswers != undefined && showRightAnswers == true && rightAnswer == false}" :style="progressWidth"></div>
            </div>   
        </div>

    </div>
</template>
  
<script>
import { mapGetters } from 'vuex'
export default {
    name: 'ProgressBar',
    props: {
        showRightAnswers: Boolean,
        rightAnswer: Boolean,
        progressName: String,
        answerVotes: Number,
        summaryVotes: Number
    },
    computed: {
      ...mapGetters([
          "actualView"
      ]),
      formattedProgressText() {
      return `${this.progressName} (${this.answerVotes} человек)`;
    },
        progressWidth(){
            console.log(this.showRightAnswers)
            if (this.answerVotes === 0) {
                return { "width": 0}
            }
            else{
                return { "width": ( this.answerVotes / this.summaryVotes * 100) + "%"}
            }
        }
    },
    methods: {
        getImage() {
            return require(`@/assets/images/Zambon-2/tick.svg`)
        }
    }  
}
</script>
  
