<template>
    <!-- <h1 class="text-center text-red-600 font-inter-medium text-lg py-6 border-b border-slate-700"> -->
    <h1 
        :style="textColor"    
        class="main-title"  
        :class="{'first-main-title': actualView.InternalID === 1,}"
        v-html="titleText"      
    >

  </h1>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
    name: 'GameTitle',
    props: {
        titleText: String,
        titleColor: String
    },
    computed:{
        ...mapGetters([
          "actualView",
      ]),
        textColor(){
            if (this.titleColor) {
                return {"color": this.titleColor}
            }
            else{
                return ""
            }
        }        
    }
}
</script>

