<template>
    <div
      class="modal-warning"
    >    
        <div
            v-if="correct === 'correct'"
            class="correct-warning"
        >
        
            <img src="@/assets/images/MidOtit/Right.svg" alt="">
                <div class="warning-text">
                    {{warningText}}
                </div>
                <button
                    class="correct-modal-button"
                    @click="modalButtonClick(correct)"
                >
                    Ок
                
                </button>
                
        </div>
        <div
            v-else-if="correct === 'notCorrect'"
            class="not-correct-warning"
        >
        <img src="@/assets/images/MidOtit/Wrong.svg" alt="">
            <div
                v-html="warningText"
                class="warning-text">
            </div>
        </div>
        <div
            v-else-if="correct === 'partlyCorrect'"
            class="partly-correct-warning"
        >
            <div
                v-html="warningText"
                class="warning-text warning-text-hint">
            </div>
        </div>
        
    </div>      
</template>

<script>
export default {
    name: 'ModalWarning',
    props: {
        correct: String,
        warningText: String
    },
    methods: {
        modalButtonClick(){
            this.$emit('modalButtonClick', this.correct)
        }
    }
}
</script>

