<template>
    <!-- <button -->
    <div
      class="footer-button"
      :class="{
          'footer-button-end': buttonStyle === 'startAgain'
        }"
      @click="buttonClick"
    >
      <div class="footer-button-text" >{{buttonData.name}}</div>

    </div>
    <!-- </button> -->
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    name: 'FooterButton',
    props: {
      buttonData: {},
      buttonStyle: Number,
    },
    computed: {
      ...mapGetters([
          "actualProperty",
      ]),
    },
    methods: {
      buttonClick(){
        this.$emit('buttonClick', this.buttonData.action)
      }
    }
}
</script>

<style>
  .footer-button-text{
    margin-left: 20px;
    margin-right: 20px;
  }
</style>